const {cos, sin, acos, tan, hypot, floor, PI} = Math;

const rad_deg = PI / 180;
const radians = deg => deg * rad_deg;
export const degrees = rad => rad / rad_deg;
const scl = (a, c) => a.map(ai => ai * c);
const divs = (a, s) => a.map(ai => ai / s);
const add = (a, b) => a.map((ai, i) => ai + b[i]);
const dot = (a, b) => a.reduce((t, ai, i) => t + ai * b[i], 0);


export const tangent_vector = (Incl = 0, Azim = 0) => {
	const sinI = sin(Incl);
	return [sinI * cos(Azim), sinI * sin(Azim), cos(Incl)];
}
const dogleg = (t, t0 = [0, 0, 1]) => acos(dot(t0, t));
const shapeFactor = DL => {
	if (DL === 0) return 1;
	const a = .5 * DL;
	return tan(a) / a;
}
const minimum_curvuture_method = (t, t0 = [0, 0, 1], dMD = 0, p0 = [0, 0, 0], DL = dogleg(t, t0)) => {
	return add(p0, scl(add(t, t0), .5 * dMD * shapeFactor(DL)));
}
const interpolation = (t2, t1 = [0, 0, 1], DL1, DL = 0) => DL === 0 ? t1 : divs(add(scl(t1, sin(DL - DL1)), scl(t2, sin(DL1))), sin(DL));
export const Survey = (data) => {
	let t0 = [0, 0, 1], p0 = [0, 0, 0];
	const survey = data.map(([MD, Incl, Azim], i) => {
		const t = tangent_vector(radians(Incl || 0), radians(Azim || 0));
		const DL = dogleg(t, t0);
		const dMD = i > 0 ? MD - data[i - 1][0] : MD;
		const [NS, EW, TVD] = p0 = minimum_curvuture_method(t, t0, dMD, p0, DL);
		const DLS = DL === 0 && dMD === 0 ? 0 : DL / dMD;
		t0 = t;
		const HD = hypot(NS, EW);
		return {MD, dMD, Incl, Azim, DLS, NS, EW, TVD, HD, t, DL};
	})
	return survey;
}
export const smooth = (survey, dog_leg_max = 1) => {
	const interp = interpolate(survey);
	const new_survey = [];
	survey.forEach((d, i) => {
		const n = floor(degrees(d.DL) / dog_leg_max);
        if (n > 0) {
			const dMD = (d.MD - survey[i - 1]?.MD) / (n + 1);
			const ds = Array(n).fill(0).map((_, j) => interp(d.MD - (n - j) * dMD));
			new_survey.push(...ds);
        }
		new_survey.push(d);
    })
	return new_survey;
}
export const interpolate = (survey) => {
	const interp = MD => {
		const i = survey.findIndex(c => c.MD > MD);
		const {t: t2, DLS = 0, DL = 0} = survey[i] ?? {};
		const {t: t1, MD: MD1 = 0, NS: NS1 = 0, EW: EW1 = 0, TVD: TVD1 = 0} = i === 0 ? {} : survey[i - 1] ?? survey.at(-1);

		const dMD = MD - MD1;
		const DL1 = DLS * dMD;
		const t = interpolation(t2, t1, DL1, DL);
		const [NS, EW, TVD] = minimum_curvuture_method(t, t1, dMD, [NS1, EW1, TVD1], DL1);
		return {MD, NS, EW, TVD, DLS};
	};
	return interp;
}